<template>
  <div>
    <list-basic :config='basicConfig'></list-basic>
    <list-basic :config='businessConfig'></list-basic>
  </div>
</template>
<script>
export default {
  // name: 'List',
  data () {
    return {
      basicConfig: null, // ,
      businessConfig: null // 
    }
  },
  created () {
    this.init()
  },
  methods: {
    init () {
      this.basicConfig = Object.assign({}, this.basicConfig, {
        title: '',
        list: [
          {
            name: 'buttonSetTime ',
            path: '/views/buttonSetTime',
            clickAble: false
          },
          {
            name: 'fileUpLoad ',
            path: '/views/buttonSetTime'
          }
        ]
      })
      this.businessConfig = {
        title: '',
        list: [
          {
            name: 'GoJs ',
            path: '/views/buttonSetTime',
            clickAble: false
          },
          {
            name: 'ReadFile ',
            path: '/views/buttonSetTime'
          }
        ]
      }
    }
  }
}
</script>



